      margin-left: 10px;
      &.open .el-icon-arrow-left {
        transform: rotate(90deg);
      }
      &.close .el-icon-arrow-left {
        transform: rotate(-90deg);
      }
    }
    .el-icon-arrow-left {
      transition: all 0.3s linear;
    }
  }
  .sh_slider_btns {
    // width: 36px;
    display: flex;
    align-items: flex-start;
    .el-button {
      // width: 28px;
